.signup-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.signup-header {
    font-size: 60px;
    font-weight: bold;
}

.signup-form {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    margin-top: 50px;
}

label {
    font-size: 20px;
}

input {
    width: 450px;
    height: 25px;
    padding: 10px;
}

.signup-page-btn {
    display: flex;
    justify-content: center;
}

.signup-btn {
    width: 200px;
    height: 50px;
    border-radius: 20px;
    font-size: 20px;
    cursor: pointer;
}

.signup-btn:disabled {
    background-color: #ccc;
    color: #666;
    cursor: not-allowed;
    border: 1px solid #ccc;
}

.signup-btn:disabled:hover {
    background-color: #ccc;
    color: #666;
    cursor: not-allowed;
    border: 1px solid #ccc;
}

.signup-btn.active {
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    border: 1px solid #007bff;
}

.signup-btn.active:hover {
    background-color: #0056b3;
    border: 1px solid #0056b3;
    cursor: pointer;
}